import clsx from 'clsx'
import { useStore, actions } from '../../../../store';
import './LineSelector.css';

function Summary(styles) {
    const componentStyle = styles.styles
    const [state, dispatch] = useStore()
    const { summary } = state

    const storeString = (target, content) => {
        dispatch(actions.setString(target, content))
    }

    return (
        <div className={componentStyle.unitBody}>
            <div className={componentStyle.lineTextWrapper}>
                <p 
                    className={clsx(componentStyle.lineText, "editMode")}
                    contentEditable="true"
                    suppressContentEditableWarning={true}
                    onInput={(e) => storeString('summary', e.target.textContent)}
                >{summary}</p>
            </div>
        </div>
    )
}

export default Summary