import clsx from 'clsx'
import { useStore, actions } from '../../../../store';

function Header(styles) {
    const componentStyles = styles.styles
    const [state, dispatch] = useStore()
    const { name, jobPosition } = state

    const storeString = (target, content) => {
        dispatch(actions.setString(target, content))
    }

    return (
        <section className={componentStyles.cvHeader}>
            <h1 
                className={clsx(componentStyles.cvHeading, "editMode")}
                onInput={(e) => storeString('name', e.target.textContent)}
                contentEditable="true"
                suppressContentEditableWarning={true}
            >{name}</h1>
            <p 
                className={clsx(componentStyles.cvJobDesc, "editMode")}
                onInput={(e) => storeString('jobPosition', e.target.textContent)}
                contentEditable="true"
                suppressContentEditableWarning={true}
            >{jobPosition}</p>
        </section>
    )
}

export default Header