import { useEffect } from 'react';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "../slick-override.css";
import Slider from 'react-slick';
import { Link } from 'react-router-dom';
import { useStore, actions } from '../../../../store'

function SlickTemplate() {
    const [state, dispatch] = useStore()
    const { currentTemplate } = state
        
    useEffect(() => {
        document.querySelectorAll(".slick__template-item").forEach(template => {
            if (template.id === currentTemplate) {
                template.querySelector(".slick__template-img").className += " image-active"
                template.querySelector(".slick__template-name").className += " active"
            }
            template.addEventListener("mousedown", handleTemplateActive)
        })
        return () => {
            document.querySelectorAll(".slick__template-item").forEach(template => {
                template.removeEventListener("mousedown", handleTemplateActive)
            })
        }
    }, [])
    
    const settings = {
        dots: false,
        speed: 800,
        slidesToShow: 1,
        slidesToScroll: 1,
    };
    
    function handleTemplateActive() {
        let curActiveImg = document.querySelectorAll(".slick__template-img.image-active")[0]
        curActiveImg.className = curActiveImg.className.replace(" image-active", "")
        let curActiveName = document.querySelectorAll(".slick__template-name.active")[0]
        curActiveName.className = curActiveName.className.replace(" active", "")
        let thisTemplateImg = this.querySelector("a>.slick__template-img")
        thisTemplateImg.className += " image-active"
        let thisTemplateName = this.querySelector("a>.slick__template-name")
        thisTemplateName.className += " active"

        dispatch(actions.setString('currentTemplate', this.id))
    }

    return (
        <Slider {...settings}>
            <ul className="slick__template-list"> 
                <li className="slick__template-item" id="chicken">
                    <Link to="chicken-template" style={{color: "#fff"}}>
                        <div 
                            className="slick__template-img"
                            style={{backgroundImage: "url('https://mycv.vn/assets/images/templates/chicken.png')"}}
                        ></div>
                        <h4 className="slick__template-name">Chicken 
                            <span className="tag template__tag not-available">Chưa có</span>
                        </h4>
                    </Link>
                </li>
                <li className="slick__template-item" id="basic">
                    <Link to="basic-template" style={{color: "#fff"}}>
                        <div 
                            className="slick__template-img"
                            style={{backgroundImage: "url('https://mycv.vn/assets/images/templates/basic.png')"}}
                        ></div>
                        <h4 className="slick__template-name">Basic
                            <span className="tag template__tag available">Free</span>
                        </h4>
                    </Link>
                </li>
                <li className="slick__template-item" id="cascade">
                    <Link to="cascade-template" style={{color: "#fff"}}>
                        <div 
                            className="slick__template-img"
                            style={{backgroundImage: "url('https://mycv.vn/assets/images/templates/cascade.png')"}}
                        ></div>
                        <h4 className="slick__template-name">Cascade
                            <span className="tag template__tag available">Free</span>
                        </h4>
                    </Link>
                </li>
                <li className="slick__template-item" id="crisp">
                    <Link to="crisp-template" style={{color: "#fff"}}>
                        <div 
                            className="slick__template-img"
                            style={{backgroundImage: "url('https://mycv.vn/assets/images/templates/crisp.png')"}}
                        ></div>
                        <h4 className="slick__template-name">Crisp
                            <span className="tag template__tag not-available">Chưa có</span>
                        </h4>
                    </Link>
                </li>
            </ul>
            <ul className="slick__template-list"> 
                <li className="slick__template-item" id="iconic">
                    <Link to="iconic-template" style={{color: "#fff"}}>
                        <div 
                            className="slick__template-img"
                            style={{backgroundImage: "url('https://mycv.vn/assets/images/templates/iconic.png')"}}
                        ></div>
                        <h4 className="slick__template-name">Iconic
                            <span className="tag template__tag not-available">Chưa có</span>
                        </h4>
                    </Link>
                </li>
                <li className="slick__template-item" id="vibes">
                    <Link to="vibes-template" style={{color: "#fff"}}>
                        <div 
                            className="slick__template-img"
                            style={{backgroundImage: "url('https://mycv.vn/assets/images/templates/vibes.png')"}}
                        ></div>
                        <h4 className="slick__template-name">Vibes
                            <span className="tag template__tag not-available">Chưa có</span>
                        </h4>
                    </Link>
                </li>
                <li className="slick__template-item" id="simple">
                    <Link to="simple-template" style={{color: "#fff"}}>
                        <div 
                            className="slick__template-img"
                            style={{backgroundImage: "url('https://mycv.vn/assets/images/templates/simple.png')"}}
                        ></div>
                        <h4 className="slick__template-name">Simple
                            <span className="tag template__tag not-available">Chưa có</span>
                        </h4>
                    </Link>
                </li>
                <li className="slick__template-item" id="enfold">
                    <Link to="enfold-template" style={{color: "#fff"}}>
                        <div 
                            className="slick__template-img"
                            style={{backgroundImage: "url('https://mycv.vn/assets/images/templates/enfold.png')"}}
                        ></div>
                        <h4 className="slick__template-name">Enfold
                            <span className="tag template__tag available">Free</span>
                        </h4>
                    </Link>
                </li>
            </ul>
        </Slider>
    );
}

export default SlickTemplate