import { useState } from 'react';
import clsx from 'clsx';
import { useStore, actions } from '../../../../store';
import './LineSelector.css';

function Contact({styles, selectorStyle, unitLineStyle}) {
    const componentStyle = styles
    const [state, dispatch] = useStore()
    const [update, setUpdate] = useState(0)
    const { contacts } = state;

    const storeUnitSubtitle = (index, element, target) => {
        dispatch(actions.setUnitSubtitle(index, element, target))
    }

    const storeUnitInfoDesc = (index, element, target) => {
        dispatch(actions.setUnitInfoDesc(index, element, target))
    }

    const handleDeleteLine = (e, index, target) => {
        e.stopPropagation()
        dispatch(actions.deleteLine(index, target))
        setUpdate(update - 1)
    }

    const handleAddLine = (e, index, target) => {
        e.stopPropagation()
        dispatch(actions.addLine(index, target))
        setUpdate(update + 1)
    }

    const activeLineSelector = (e, selector, index) => {
        document.getElementById(`${selector}-lineSelector-${index}`).style.display = "flex";
    }

    const deActiveLineSelector = (e, selector, index) => {
        document.getElementById(`${selector}-lineSelector-${index}`).style.display = "none";
    }



    return (
        <div className={componentStyle.unitBody}>
            {contacts.map((contact, index) => (
                <div 
                    className={componentStyle.unitLine}
                    style={unitLineStyle}
                    key={index}
                    onFocus={(e) => activeLineSelector(e, 'Contact', index)}
                    onBlur={(e) => deActiveLineSelector(e, 'Contact', index)}
                >
                    <div 
                        className="lineSelector" 
                        id={`Contact-lineSelector-${index}`}
                        style={selectorStyle}
                    >
                        <button 
                            className="btn lineBtn"
                            id="addLineBtn"
                            onMouseDown={e => handleAddLine(e, index, 'contacts')}
                        >
                            <i className="fas fa-plus-circle"></i>
                            <span className="btnNote left-note">Thêm mục ở dưới</span>
                        </button>
                        <button 
                            className="btn lineBtn"
                            id="dltLineBtn"
                            onMouseDown={(e) => handleDeleteLine(e, index, 'contacts')}
                        >
                            <i className="fas fa-trash-alt"></i>
                            <span className="btnNote right-note">Gỡ bỏ mục này</span>
                        </button>
                    </div>
                    <div className={clsx(componentStyle.lineTitleWrapper, "zIndex-2")}>
                        <span 
                            className={clsx(componentStyle.lineTitle, "editMode")}
                            contentEditable="true"
                            suppressContentEditableWarning={true}
                            onInput={(e) => storeUnitSubtitle(index, e.target, 'contacts')}
                        >{contact.subtitle}
                        </span>
                    </div>
                    <div 
                        className={clsx(componentStyle.lineDescription, "editMode", "zIndex-2")}
                        contentEditable="true"
                        suppressContentEditableWarning={true}
                        onInput={(e) => storeUnitInfoDesc(index, e.target, 'contacts')}
                    >{contact.infoDesc}
                    </div>
                </div>
            ))}
        </div>
    )
}

export default Contact