import { useState } from 'react'
import clsx from 'clsx'
import { useStore, actions } from '../../../../store';
import './LineSelector.css';

function Experience({ styles, lineTitleStyle, lineDescStyle}) {
    const componentStyle = styles
    const [update, setUpdate] = useState(0)
    const [state, dispatch] = useStore()
    const { experts } = state

    const storeUnitSubtitle = (index, element, target) => {
        dispatch(actions.setUnitSubtitle(index, element, target))
    }

    const storeUnitDate = (index, element, target) => {
        dispatch(actions.setUnitDate(index, element, target))
    }

    const storeListDesc = (index, element, target) => {
        const list = element.querySelectorAll("li")
        const data = Array.from(list).map(item => item.innerText)
        dispatch(actions.setListDesc(index, data, target))
    }

    const handleDeleteLine = (e, index, target) => {
        e.stopPropagation()
        dispatch(actions.deleteLine(index, target))
        setUpdate(update - 1)
    }

    const handleAddLineGroup = (e, index, target) => {
        e.stopPropagation()
        dispatch(actions.addLineGroup(index, target))
        setUpdate(update + 1)
    }

    const activeLineSelector = (selector, index) => {
        document.getElementById(`${selector}-lineSelector-${index}`).style.display = "flex";
    }

    const deActiveLineSelector = (selector, index) => {
        document.getElementById(`${selector}-lineSelector-${index}`).style.display = "none";
    }

    return (
        <div className={componentStyle.unitBody}>
            {experts.map((expert, index) => (
                <div 
                    className={clsx(componentStyle.unitLine, "d-flex")} 
                    key={index}
                    onFocus={() => activeLineSelector('Experience', index)}
                    onBlur={() => deActiveLineSelector('Experience', index)}
                >
                    <div 
                        className="lineSelector" 
                        id={`Experience-lineSelector-${index}`}
                    >
                        <button 
                            className="btn lineBtn"
                            id="addLineBtn"
                            onMouseDown={e => handleAddLineGroup(e, index, 'experts')}
                        >
                            <i className="fas fa-plus-circle"></i>
                            <span className="btnNote left-note">Thêm mục ở dưới</span>
                        </button>
                        <button 
                            className="btn lineBtn"
                            id="dltLineBtn"
                            onMouseDown={(e) => handleDeleteLine(e, index, 'experts')}
                        >
                            <i className="fas fa-trash-alt"></i>
                            <span className="btnNote right-note">Gỡ bỏ mục này</span>
                        </button>
                    </div>
                    <div className={clsx(componentStyle.lineTitleWrapper, "zIndex-2")}>
                        <span 
                            className={clsx(componentStyle.lineTitle, "editMode")}
                            style={lineTitleStyle}
                            contentEditable="true"
                            suppressContentEditableWarning={true}
                            onInput={(e) => storeUnitDate(index, e.target, 'experts')}
                        >{expert.date}
                        </span>
                    </div>
                    <div 
                        className={clsx(componentStyle.lineDescription, "zIndex-2")}
                        style={lineDescStyle}
                    >
                        <div 
                            className={clsx(componentStyle.lineSubtitle, "editMode", "zIndex-2")}
                            contentEditable="true"
                            suppressContentEditableWarning={true}
                            onInput={(e) => storeUnitSubtitle(index, e.target, 'experts')}
                        >{expert.subtitle}</div>
                        <ul 
                            className={clsx(componentStyle.lineList, "editMode", "zIndex-2")}
                            contentEditable="true"
                            suppressContentEditableWarning={true}
                            onInput={(e) => storeListDesc(index, e.target, 'experts')}
                        >
                            {expert.jobs.map((job, index) => (
                                <li className={componentStyle.subLine} key={index}>{job}</li>
                            ))}
                        </ul>
                    </div>
                </div>
            ))}
        </div>
    )
}

export default Experience