import { useEffect } from 'react'
import styles from './ProfilePhoto.module.css'
import { useStore, actions } from '../../../../../store'

function ProfilePhoto() {
    const [state, dispatch] = useStore()
    const { profilePhotoURL } = state

    useEffect(() => {
        let photoUpload = document.getElementById(`${styles.photoUpload}`)
        photoUpload.addEventListener("change", handleUploadProfile)
        return () => {
            if (photoUpload) {
                photoUpload.removeEventListener("change", handleUploadProfile)
            }
        }
    }, [profilePhotoURL])
    
    function handleUploadProfile() {
        let reader = new FileReader()
        let file = this.files[0]
        reader.readAsDataURL(file)
        if (file) {
            reader.addEventListener("load", (e) => {
                dispatch(actions.setProfilePhoto(e.target.result))
            })
        }
    }

    return (
        <div id={styles.photoContainer}>
            <img 
                src={profilePhotoURL} 
                alt="" 
                id={styles.photoPlaceholder}
            />
            <input type="file" id={styles.photoUpload}/>
            <label htmlFor={styles.photoUpload} id={styles.uploadPhotoBtn}>
                <i className="fas fa-upload"></i>
            </label>
        </div>
    )
}

export default ProfilePhoto